import { intialStateType } from "../Types/Type";


const SEARCH = "SEARCH";


let intialState:intialStateType = {
    search: "",
}

const Search_reducer = (state = intialState, action:any):intialStateType => {

    switch (action.type) {

        case SEARCH: {
            return {...state,search:action.text}

        }
        default:
            return state
    }
}

type SearchSuccess=(text:string)=>{
    type:typeof SEARCH,
    text:string
}
export const SearchSuccess:SearchSuccess=(text)=>{
    return{
        type:SEARCH,
        text
    }
}

export default Search_reducer;

