import {usersApi} from "../Api/Api";
import { UsersType } from "../Types/Type";


const FOLLOW = "FOLLOW";
const UNFOLLOW = "UNFOLLOW";
const SETUSERS = "SETUSERS"
const SETCURRENTPAGE = "SETCURRENTPAGE"
const SETTOTELUSERSCOUNT = "SETTOTELUSERSCOUNT"
const FOLLOWING_PROGRESS = "FOLLOWING-PROGRESS"


let intialState = {
    users: [] as Array<UsersType>,
    pageSize: 9,
    totalUsersCount: 0,
    currentPage: 1,
    followingProgress: [] as Array<number> , //Array of users id
    isFetching: false,
    page: [] as Array<number>
}

type intialStateType=typeof intialState

const Users_reducer = (state = intialState, action:any):intialStateType => {

    switch (action.type) {

        case SETUSERS: {
            return {
                ...state, users: action.user
            }
        }

        case FOLLOW: {
            return {
                ...state, users: state.users.map(u => {
                    if (u.id === action.userid) {
                        return {...u, followed: true}
                    }
                    return u
                })
            }
        }

        case UNFOLLOW: {
            return {
                ...state, users: state.users.map(u => {
                    if (u.id === action.userid) {
                        return {...u, followed: false}
                    }
                    return u
                })
            }
        }

        case SETCURRENTPAGE: {
            return {
                ...state, currentPage: action.page
            }
        }

        case SETTOTELUSERSCOUNT: {
            return {
                ...state, totalUsersCount: action.count
            }
        }

        case FOLLOWING_PROGRESS: {
            return {
                ...state,
                followingProgress: action.follow ? [...state.followingProgress, action.userid]
                    : state.followingProgress.filter(id => id != action.userid)
            }
        }

        default:
            return state
    }
}

export default Users_reducer;

type FollowType=(userid:number)=>{
    type:typeof FOLLOW,
    userid:number
}

export const Follow:FollowType = (userid) => {

    return {
        type: FOLLOW,
        userid: userid
    }
}

type UnFollowType=(userid:number)=>{
    type:typeof UNFOLLOW,
    userid:number
}

export const UnFollow:UnFollowType = (userid) => {
    return {
        type: UNFOLLOW,
        userid: userid
    }
}

type SetUsersType=(user:Array<UsersType>)=>{
    type:typeof SETUSERS,
    user:Array<UsersType>
}

export const SetUsers:SetUsersType = (user) => {
    return {
        type: SETUSERS,
        user: user
    }
}

type SetCurrentPageType=(page:number)=>{
    type:typeof SETCURRENTPAGE,
    page:number
}

export const SetCurrentPage:SetCurrentPageType = (page) => {
    return {
        type: SETCURRENTPAGE,
        page: page
    }
}

type SetTotalUsersCountType=(count:number)=>{
    type:typeof SETTOTELUSERSCOUNT,
    count:number
}

export const SetTotalUsersCount:SetTotalUsersCountType = (count) => {
    return {
        type: SETTOTELUSERSCOUNT,
        count: count
    }
}

type FollowingProgressType=(follow:any, userid:number)=>{
    type:typeof FOLLOWING_PROGRESS,
    follow:any,
    userid:number
}

export const FollowingProgress:FollowingProgressType = (follow, userid) => {
    return {
        type: FOLLOWING_PROGRESS,
        follow, userid
    }
}

export const UsersThunk = (currentPage:number, pageSize:number) => async (dispatch:any) => {

    let response = await usersApi.getUsers(currentPage, pageSize)

    dispatch(SetUsers(response.items))
    dispatch(SetTotalUsersCount(response.totalCount))
}

export const CurrentPageThunk = (pageNumber:number, pageSize:number) => async (dispatch:any) => {

    let response = await usersApi.getUsers(pageNumber, pageSize)

    dispatch(SetCurrentPage(pageNumber))
    dispatch(SetUsers(response.items))
}

const followUnollowFlow=async (dispatch:any,userId:number,apiMethod:any,actionCreator:any)=>{

    let response = await apiMethod(userId)
    
    dispatch(FollowingProgress(true, userId))

    if (response.data.resultCode === 0) {
        dispatch(actionCreator(userId))
    }
    dispatch(FollowingProgress(false, userId))
}

export const FollowSuccess = (userId:number) => async (dispatch:any) => {
    followUnollowFlow(dispatch, userId, usersApi.unFollowAxios.bind(userId),UnFollow)
}

export const UnFollowSuccess = (userId:number) => async (dispatch:any) => {
    followUnollowFlow(dispatch, userId, usersApi.followAxios.bind(userId),Follow)
}



