import {Loginthunk} from "./Auth-reducer";

const INITIALIZED_SUCCESS:string="SET-SUCCESS"


type initializedSuccessActionCreator={
    type:typeof INITIALIZED_SUCCESS
}


let intialState = {
    initialized:<Boolean>false,
}


const App_reducer=(state:any=intialState,action:any):initializedSuccessActionCreator=>{

    switch (action.type) {
        
        case INITIALIZED_SUCCESS:{
            return {
                ...state,
                initialized:true
            }
        }
        default:
            return state
    }
}

export const initializedSuccessActionCreator:initializedSuccessActionCreator = {
        type: INITIALIZED_SUCCESS,
}

export const initializationthunk= () =>(dispatch:any)=>{

    let promise=dispatch(Loginthunk())

    promise.then(()=>{
        dispatch(initializedSuccessActionCreator)
    })
}

export default App_reducer;







