import {LoginApi, SecurityApi, usersApi} from "../Api/Api";


const SET_USER_DATA:string = "SET-USER-DATA"
const LOGIN_ERROR:string = "LOGIN-ERROR"
const GET_CAPTCHA_URL:string = "GET-CAPTCHA-URL"
const LOGOUT_Remove:string="LOGOUT_Remove"

type setIntialState = {
    userId:number,
    email:string|null,
    login: string|null,
    isAuth: boolean,
    error: boolean,
    captcha:any,

}

let intialState:setIntialState = {
    userId:15498,
    email:null,
    login: null,
    isAuth: false,
    error: false,
    captcha:null,

}

const Auth_reducer = (state:any = intialState, action:any):setIntialState => {

    switch (action.type) {
        
        case SET_USER_DATA: {
            return {
                ...state,
                ...state.payload,
                isAuth: true,

            }
        }

        case LOGIN_ERROR: {
            return {
                ...state, error: action.message
            }
        }

        case GET_CAPTCHA_URL: {
            debugger
            return {
                ...state, captcha: action.url
            }
        }

        case LOGOUT_Remove:{
            debugger
            return {
                ...state,error:true
            }
        }

        default:
            return state
    }
}

export default Auth_reducer;

type setUserDataType =(Id:string|null, email:string|null, login:string|null, isAuth:boolean)=>{
    type:typeof SET_USER_DATA,
    payload: {
        Id:string|null, email:string|null, login:string|null, isAuth:boolean
    }
}

export const SetUserData:setUserDataType = (Id, email, login, isAuth) => {
    return {
        type: SET_USER_DATA,
        payload: {
            Id, email, login, isAuth
        }
    }
}

type logoutRemoveType ={
    type:typeof LOGOUT_Remove
}

export const logoutRemove:logoutRemoveType={
        type:LOGOUT_Remove,
}

type loginErrorType=(message:string)=>{
    type:typeof LOGIN_ERROR,
    message:string
}
export const loginError:loginErrorType = (message) => {
    return{
        type: LOGIN_ERROR,
        message
    }
}

type captchaUrlType=(url:string)=>{
    type:typeof GET_CAPTCHA_URL,
    url:string
}

export const captchaUrl:captchaUrlType = (url) => {
    return {
        type: GET_CAPTCHA_URL,
        url
    }
}

export const Loginthunk = () => async (dispatch:any) => {

    let response = await usersApi.loginAxios()

    if (response.data.resultCode === 0) {
        let {Id, email, login,} = response.data.data;
        dispatch(SetUserData(Id, email, login, true))
    }

}

export const getCaptchathunk = () => async (dispatch:any) => {

    let response = await SecurityApi.securityCaptchaUrl()

    const captcha=response.data.url
    dispatch(captchaUrl(captcha))

}

export const Loginingthunk = (email:string, password:string, rememberMe:boolean,captcha:string) => async (dispatch:any) => {

    let response = await LoginApi.LoginAxios(email, password, rememberMe,captcha)

    if (response.data.resultCode === 0) {
        dispatch(Loginthunk())
    }else{
        if(response.data.resultCode === 10){
            dispatch(getCaptchathunk())
        }
        console.log("Error get captcha")
    }
}

export const Logoutthunk = () => async (dispatch:any) => {

    let response = await LoginApi.LogoutAxios()

    if (response.data.resultCode === 0) {
        dispatch(SetUserData(null, null, null, false))
        window.location.reload()
    } else {
        let message = response.data.messages.length > 0 ? response.data.messages[0] : "Login error";
        dispatch(loginError(message))
    }
}






