import nature from "../assets/images/mountains.jpg";
import lake from "../assets/images/158842813619331512.jpg";
import sea from "../assets/images/mesmerising-views-unforgettabl.jpg";

const ADD_MESSEGE = "ADD-MESSEGE"
const UPDATE_NEW_MESSEGE_TEXT = "UPDATE-NEW-MESSEGE-TEXT"

type Dialogs={
    id:string,
    name:string
}

type Messages={
    id:string,
    name:string,
    messege:string,
    image:any,
}

let intialState = {
    Dialogs: [
        {id: "1", name: "Dima",},
        {id: "7", name: "Vova",},
        {id: "2", name: "Vasy",}
    ] as Array<Dialogs>,

    Messeges: [
        {id: "1",name: "Dima", messege: "Hi", image:nature},
        {id: "1",name: "Vova", messege: "Lorem ipsum dolor sit amet, consectetur adipiscing elit,\n" +
                "sed do eiusmod tempor incididunt ut labore et dolore.\n" +
                "magna aliqua.",image:lake},
        {id: "1",name: "Vasy", messege: "Ut enim ad minim veniam, quis nostrud\n" +
                "exercitation ullamco laboris nisi ut aliquip ex ea\n" +
                "commodo consequat.",image:sea},

    ] as Array<Messages>,

    NewMessegetext: ""
}

export type intialStateType=typeof intialState

const DialogPage_reducer = (state = intialState, action:any) => {

    switch (action.type) {
        
        case ADD_MESSEGE: {
            let newmessege = {
                id: "4",
                messege: action.text.replace(/^[^"]*"|".*/g, ""),
            }
            return {
                ...state,
                NewMessegetext: "",
                Messeges: [...state.Messeges, newmessege]
            }
        }

        case UPDATE_NEW_MESSEGE_TEXT: {
            return {...state, NewMessegetext: action.text}
        }
        default:
            return state
    }
}

type AddMessegeActionCreatorType=(text:string)=>{
    type:typeof ADD_MESSEGE,
    text:string
}

export const AddMessegeActionCreator:AddMessegeActionCreatorType = (text) => {

    return {
        type: ADD_MESSEGE,
        text: text
    }
}

type UpdateMessegeActionCreatorType=(text:string)=>{
    type:typeof UPDATE_NEW_MESSEGE_TEXT,
    text:string
}

export const UpdateMessegeActionCreator:UpdateMessegeActionCreatorType = (text) => {
    return {
        type: UPDATE_NEW_MESSEGE_TEXT,
        text: text
    }
}

export default DialogPage_reducer;